.Result {
    text-align: center;
}

.Result-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .Result-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.Result-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.Result-link {
    color: #61dafb;
}

@keyframes Result-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

#RatingDisplayContainer {
    width: 80%;
}

#Top-info-text {
    margin-bottom: 20px;
}

#Lichess-display, #Estimated-display, #Fide-display {
    margin-bottom: 20px;
}